
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.call-to-action {
  font-size: 24px;
  font-weight: 200;
}

.date-to-guess {
  margin: 1rem 0 3rem;
  font-size: 48px;
  font-weight: 600;
}

.buttons {
  display: grid;
  grid-gap: 1rem;
  grid-auto-flow: column;
}

.status {
  font-size: 24px;
  height: 32px;
  margin-bottom: 2rem;
  font-weight: 200;
}

.correct {
  color: forestgreen;
}

.incorrect {
  color: crimson;
}

.breakdown {
  height: 144px;
  margin-top: 2rem;
  width: 100%;
}

.breakdown-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.breakdown-content .entry {
  height: 24px;
  display: flex;
}

.breakdown-content .entry .element {
  flex: 0 1 60px;
}
.breakdown-content .entry .value {
  flex: 0 1 300px;
}
.breakdown-content .entry.sum {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
